import isEmpty from "./isEmpty"


const validateUserCreation = (userData) => {
    const errors = {}


    if (isEmpty(userData.fname)) {
        errors.fname = 'First Name cannot be empty.'
    }
    if (isEmpty(userData.lname)) {
        errors.lname = 'Last name can\'t be empty.'
    }
    if (isEmpty(userData.username)) {
        errors.username = 'User name can\'t be empty.'
    }
    if (isEmpty(userData.email)) {
        errors.email = 'Email can\'t be empty.'
    }

    if (userData.email && !/^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/.test(userData.email)) {
        errors.email = 'Email not valid.'

    }

    if (isEmpty(userData.password2)) {
        errors.password2 = 'Confirm the password.'
    }

    if (userData.password && userData.password2 && userData.password !== userData.password2) {
        errors.password2 = 'Passwords must match.'
    }

    if (isEmpty(userData.role)) {
        errors.role = 'Role can\'t be empty.'
    }


    //Will be valid if the errors are empty
    const isValid = isEmpty(errors);
    return { isValid, errors }
}


export { validateUserCreation }
